import tagSexAge from '@/components/common/tag-sex-age.vue'
export default {
	props:{
		item: Object,
		index: Number,
		index1: {
			type: Number,
			default: undefined
		},
		itemClass: {
			type: String,
			default: 'fadeInLeft'
		}
	},
	data () {
		return {}
	},
	components: {
		tagSexAge
	},
	methods: {
		follow (follow) {
			this.$emit('fixFollow',{
				index: this.index,
				index1: this.index1,
				follow
			})
		}
	}
}
